<template>
    <div>
        <div class="todo-header">
            <AppName />
        </div>
        <div>
            <TaskList
                :tasks="tasks"
                @createNewTask="createNewTask"
                @addTask="addTask"
                @doneTask="doneTask"
                @changeEditState="changeEditState"
            />
        </div>
    </div>
</template>

<script>
import AppName from '../atoms/AppName.vue';
import TaskList from '../organisms/TaskList.vue';

export default {
    name: 'App',
    components: {
        AppName,
        TaskList,
    },
    data() {
        return { tasks: [] };
    },
    methods: {
        createNewTask: function(task) {
            this.tasks.push(task);
        },
        addTask: function(id, name) {
            const task = this.tasks.find(task => task.id === id);
            task.name = name;
            task.isDone = false;
            task.isEdit = false;
        },
        changeEditState: function(id, isEdit) {
            const task = this.tasks.find(task => task.id === id);
            task.isEdit = isEdit;
        },
        doneTask: function(id) {
            this.tasks.find(task => task.id === id).isDone = true;
        },
    },
    async beforeMount() {
        console.log('test');
        const data = await fetch('/items');
        this.tasks = await data.json();
    },
};
</script>

<style>
.todo-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 64px;
    padding: 0 16px;
    background-color: #ffffff;
    border-bottom: 1px solid #f1f3f4;
}
</style>
