<template>
    <div>
        <div class="task-add-area">
            <TaskAddButton :onClick="createNewTask" />
        </div>
        <div class="task-list" v-for="task in computedTasks" :key="task.id">
            <TaskItem
                :id="task.id"
                :name="task.name"
                :isEdit="task.isEdit"
                @addTask="addTask"
                @doneTask="doneTask"
                @changeEditState="changeEditState"
            />
        </div>
    </div>
</template>

<script>
import TaskAddButton from '../molecules/TaskAddButton.vue';
import TaskItem from '../molecules/TaskItem.vue';

export default {
    name: 'TaskList',
    components: {
        TaskItem,
        TaskAddButton,
    },
    props: {
        tasks: { type: Array, required: true },
    },
    methods: {
        createNewTask: function() {
            const id = Math.max(...this.tasks.map(task => task.id)) + 1;
            const newTask = { id, name: '', isDone: false, isEdit: true };
            this.$emit('createNewTask', newTask);
        },
        addTask: function(id, name) {
            this.$emit('addTask', id, name);
        },
        changeEditState: function(id, isEdit) {
            this.$emit('changeEditState', id, isEdit);
        },
        doneTask: function(id) {
            this.$emit('doneTask', id);
        },
    },
    emits: ['addTask', 'doneTask', 'createNewTask', 'changeEditState'],
    computed: {
        computedTasks() {
            return this.tasks.filter(it => !it.isDone).reverse();
        },
    },
};
</script>

<style scoped>
.task-add-area {
    display: flex;
    align-items: center;
    height: 40px;
    margin: 0 2px 0 6px;
}
</style>
